<template>
  <div>
    <div class="container shadow-lg">
      <div class="row mb-2" @click="detail(item.passage.objectId)">
        <img class="img-fluid col-sm-12 col-md-4 col-lg-3 p-2 rounded"
             :src="item.coverlink"/>
        <div class="col-sm-12 col-md-8 col-lg-9 p-2">
          <h5 class="text-elipsis pl-1">{{ item.title }}</h5>
          <p class="text-elipsis-3 pt-1">
            &emsp;&emsp;{{ item.description }}
          </p>
          <div class="text-right">
            <span class="time p-1 rounded">{{ item.createdAt }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PassageItem",
  props: {
    item:Object
  },
  methods:{
    detail(objectId){
      this.$router.push({
        path: "/passage/" + objectId
      })
    }
  }
}
</script>

<style scoped>
.row:hover{
  background-color: #e7e7e7;
  cursor: pointer;
}
  img{
    height: 160px;
    object-fit: cover;
  }
  .text-elipsis-3{
    font-size: 0.9em;
    letter-spacing: 1px;
    height: 70px;
  }
  .time{
    background-color: #f8f8f8;
  }

</style>



